body {
    font-family: sans-serif;
    background-color: #333333;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: auto;
}

.App {
    border-radius: 5px;
    width: 500px;
    height: 250px;
    background-color: #fff;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
}

.App button {
    border: none;
  border-radius: 5px;
  padding: 10px 20px;
    background-color: #333333;
    color: white;
    position: absolute;
    bottom: 20px;
    right: 20px;
}

blockquote {
  margin-top: 40px;
}

blockquote:before {
    content: "\201C";
}